import { buziAPI } from '@/api';
export default {
	data() {
		return {
			inputVal: '',
			disibled: true,
			error: false
		};
	},
	watch: {
		inputVal(n, o) {
			if (n) {
				this.disibled = false;
			} else {
				this.disibled = true;
			}
		}
	},
	methods: {
		getCode() {
			let that = this;
			if (!/^1[3|4|5|7|8][0-9]\d{8,11}$/.test(this.inputVal)) {
				that.error = true;
				return;
			}
			buziAPI.getSms(
				{
					phone: that.inputVal,
					backURL: 'https%3A%2F%2Fm.weibo.cn%2F',
					st: '6f5679',
					_spr: 'screen:375x667'
				},
				res => {}
			);
		},
		empty() {
			this.error = false;
			this.inputVal = '';
		}
	}
};
